export default (sequelize, Sequelize) => {
  const SubCoursesOffered = sequelize.define("sub_courses_offered", {
    name: {
      type: Sequelize.STRING,
    },
    coursesOfferedId: {
      type: Sequelize.STRING,
    },
    typeCourseId: {
      type: Sequelize.STRING,
    },
  });
  return SubCoursesOffered;
};
